<template>
    <div class="slide-show" @mouseover="clearInv" @mouseout="runInv">
      <div class="slide-img">
        <a :href="slides[nowIndex].href">
          <transition name="slide-trans" >
            <img v-if="isShow" :src="slides[nowIndex].src"  >
          </transition>
          <transition name="slide-trans-old">
            <img v-if="!isShow" :src="slides[nowIndex].src" >
          </transition>
        </a>
      </div>
      <h2>{{slides[nowIndex].title}}</h2>
      <ul class="slide-pages">
        <li @click="goto(prevIndex)">&lt;</li>
        <li v-for="(item,index) in slides" @click="goto(index)">
          <a :class="{on:index===nowIndex}">{{index+1}}</a>
        </li>
        <li @click="goto(nextIndex)">&gt;</li>
      </ul>

    </div>
</template>

<script>
    export default {
      data() {
            return {
                nowIndex: 0,
                isShow :true
            }
      },
      methods: {
        goto(index){
          this.isShow = false
          setTimeout(()=>{
            this.nowIndex = index
            this.isShow = true
            this.$emit('onChange',index)
          },10)
        },
        runInv () {
          this.timer = setInterval(()=>{
              this.goto(this.nextIndex)
          },this.inv)
        },
        clearInv () {
            clearInterval(this.timer)
        }
      },
      props: {
        slides:{
          type:Array,
          default: []
        },
        inv:{
          default:1000
        }
      },
      computed: {
          prevIndex(){
              return this.nowIndex===0?this.slides.length-1:this.nowIndex-1;

          },
          nextIndex(){
            return this.nowIndex===this.slides.length-1?0:this.nowIndex+1;
          }
      },
      watch: { },
      components: { },
      beforeCreate () {},
      created () {},
      beforeMount () {},
      mounted () { this.runInv()},
      beforeUpdate () {},
      updated () {},
      beforeDestroy () {},
      destroyed () {}
    }
</script>

<style scoped>
  .slide-trans-enter-active {
    transition: all .5s;
  }
  .slide-trans-enter {
    transform: translateX(900px);
  }
  .slide-trans-old-leave-active {
    transition: all .5s;
    transform: translateX(-900px);
  }
  .slide-show {
    position: relative;
    margin: 15px 15px 15px 0;
    width: 900px;
    height: 500px;
    overflow: hidden;
  }
  .slide-show h2 {
    position: absolute;
    width: 100%;
    height: 100%;
    color: #fff;
    background: #000;
    opacity: .5;
    bottom: 0;
    height: 30px;
    text-align: left;
    padding-left: 15px;
  }
  .slide-img {
    width: 100%;
  }
  .slide-img img {
    width: 100%;
    position: absolute;
    top: 0;
  }
  .slide-pages {
    position: absolute;
    bottom: 10px;
    right: 15px;
  }
  .slide-pages li {
    display: inline-block;
    padding: 0 10px;
    cursor: pointer;
    color: #fff;
  }
  .slide-pages li .on {
    text-decoration: underline;
  }
</style>



